function bednumberformat(number) {
    if (number < 10) {
        return '0' + number
    }
    return number
}

function gender(gender) {
    if (gender === 2) {
        return '男'
    }
    return '女'
}

function pregancybirthdayweekday(pregancy, birthday) {
    const pregancydays = (new Date(birthday).getTime() - new Date(pregancy).getTime()) / (24 * 60 * 60 * 1000)
    const pregancyweeks = pregancydays / 7
    const pregancyweeksdays = pregancydays % 7
    return '[' + parseInt(pregancyweeks) + '+' + parseInt(pregancyweeksdays) + ']'
}

