function pregancytolocaldays(pregancy) {
    const pregancydays = (new Date().getTime() - new Date(pregancy).getTime()) / (24 * 60 * 60 * 1000)

    return parseInt(pregancydays % 7)
}

function pregancyweek(pregancy, birthday) {
    const pregancydays = (new Date(birthday).getTime() - new Date(pregancy).getTime()) / (24 * 60 * 60 * 1000)
    const pregancyweeks = pregancydays / 7
    return parseInt(pregancyweeks)
}

function pregancydays(pregancy, birthday) {
    const pregancydays = (new Date(birthday).getTime() - new Date(pregancy).getTime()) / (24 * 60 * 60 * 1000)
    const pregancyweekdays = pregancydays % 7
    return parseInt(pregancyweekdays)
}

function birthdayweeks(birthday) {
    const birthdays =( new Date().getTime() - new Date(birthday).getTime()) / (24 * 60 * 60 * 1000)
    return parseInt(birthdays / 7)
}

