import React, { Component } from 'react';
import { Link } from 'react-router-dom';
//Component
import Wardcardvalue from '../Wardcardvalue'
import { bednumberformat, gender, pregancybirthdayweekday, pregancynowweek, pregancydays, pregancyweek } from '../../Function/Wardbedfunction'
import { mmddformat } from '../../Function/Timeformat'
import './Wardcarddetail.scss'
//image
import editlogo from '../../Image/svg/edit2.svg'
import fileLogo from '../../Image/svg/file1.svg'
import file2Logo from '../../Image/svg/file2.svg'
//antdesign
import { Modal, Input, DatePicker, Button } from 'antd';
import moment from 'moment';

class Wardcarddetail extends Component {
    state = {
        hoverstate: "none",
        clickstate: false,
        mouse_hover: false,
        edit_hover: false,
        visible: false
    };

    sendData = (HISID) => {
        const { previewmode } = this.props
        console.log(HISID)
        if (previewmode) {
            this.props.parentCallback(HISID);
        }
    }

    //Module show
    handleOk(string) {
        this.setState({
            visible: false
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };
    showModal = () => {
        this.setState({
            visible: true
        });
    };
    ondatechange = (date, dateString) => {
        console.log(dateString)
    }
    render() {

        function modedisplay(mode) {
            if (mode) {
                return "none"
            }
            else {
                return "block"
            }
        }
        function modecursor(mode) {
            if (mode) {
                return "pointer"
            }
            else {
                return "default"
            }
        }
        function selectstyle(selectstate, HISID) {
            console.log(selectstate, HISID)
            if (selectstate === HISID) {
                return 'wardcarddetailborderclick'
            }
            else {
                return 'wardcarddetailborderunclick'
            }
        }
        const dateFormat = 'YYYY/MM/DD';
        const { data, selectstate, simplemode, previewmode, bednumber, wardvalue } = this.props
        const datafiliter = data.filter(function (item, index) {
            return item.bednumber === bednumber
        })
        const source = datafiliter[0]
        if (source === undefined) {
            return null
        }

        function typefrom(from) {
            if (from === 'Custom') {
                return true
            }
            return false
        }

        return (
            <div className={selectstyle(selectstate, source.HISID)}>
                <div style={{ height: '40px', display: 'flex', justifyContent: 'space-between', alignItems: 'center', backgroundColor: "rgba(215, 238, 255, 1)", padding: "9px 10px" }}>
                    <t3 style={{ color: "rgba(61, 119, 181, 1)" }}>{bednumberformat(source.bednumber)},{source.name},{gender(source.gender)},{mmddformat(source.birthday)},{source.HISID}</t3>
                    <div onClick={this.showModal} className='wardcarddetileditbutton'>
                        <img src={editlogo} alt='editlogo'></img>
                    </div>
                </div>
                <div style={{ cursor: modecursor(previewmode) }} onClick={() => this.sendData(source.HISID)}>
                    <div style={{ height: '30px', display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: "0px 10px", borderBottomWidth: "0.5px", borderBottomColor: "rgba(215, 238, 255, 1)", borderBottomStyle: "solid" }}>
                        <t2 style={{ color: "rgba(59, 151, 225, 1)" }}>{pregancybirthdayweekday(source.pregnancy, source.birthday)}&rarr;{pregancynowweek(source.pregnancy)}</t2>
                        <t4 style={{ color: "black" }}>{source.weightbirth}g&rarr;{source.weight}g</t4>
                        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", width: "70px" }}>
                            <img src={fileLogo} alt='fileLogo'></img>
                            <t4>{source.report}</t4>
                            <img src={file2Logo} alt='file2Logo'></img>
                            <t4>{source.nonreport}</t4>
                        </div>
                    </div>
                    <div style={{ display: modedisplay(simplemode), borderBottomWidth: "0.5px", borderBottomColor: "rgba(215, 238, 255, 1)", borderBottomStyle: "solid" }}>
                        <Wardcardvalue flag={source.flag} wardvalue={wardvalue} HISID={source.HISID}></Wardcardvalue>
                    </div>
                    <div style={{ height: '30px', display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: "0px 10px", position: 'relative' }}>
                        <t4>{source.note}</t4>
                        <Link className='patientviewbutton' to={{ pathname: "/nicu/patient/" + source.HISID, state: source }}>
                            <centerstyle>
                                <t4>進入病人資料頁面</t4>
                            </centerstyle>
                        </Link>
                    </div>
                </div>
                <Modal
                    width={270}
                    title="填寫病床資訊"
                    visible={this.state.visible}
                    onCancel={this.handleCancel}
                    footer={
                        <div style={{ display: "flex", justifyContent: typefrom(source.type) ? 'space-between' : 'flex-end', alignItems: "center" }}>
                            <Button key="delete" style={typefrom(source.type) ? { border: "1px solid rgba(208,2,27,1)", color: "rgba(208,2,27,1)" } : { display: "none" }} onClick={this.handleCancel}>清床</Button>
                            <div>
                                <Button key="cancel" onClick={this.handleCancel}>取消</Button>
                                <Button key="ok" type="primary" onClick={this.handleOk}>儲存</Button>
                            </div>
                        </div>}>
                    <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr 1fr 1fr", gridRowGap: "10px" }}>
                        <div className='modulecontent'>
                            <centerstyle>
                                <t2>病床號碼:</t2>
                            </centerstyle>
                            <div style={{ display: "flex", alignItems: "center" }}>
                                <div>{bednumberformat(source.bednumber)}</div>
                            </div>
                        </div>
                        <div className='modulecontent'>
                            <centerstyle>
                                <t2>病歷號碼:</t2>
                            </centerstyle>
                            <Input id="hisid_wardcard" defaultValue={source.HISID} />
                        </div>
                        <div className='modulecontent'>
                            <centerstyle>
                                <t2>出生週數:</t2>
                            </centerstyle>
                            <div style={{ display: "flex", alignItems: "center" }}>
                                <div style={{ marginRight: "20px" }}>{pregancyweek(source.pregnancy, source.birthday)}</div>
                                <t2 style={{ marginRight: "20px" }}>週</t2>
                                <div style={{ marginRight: "20px" }}>{pregancydays(source.pregnancy, source.birthday)}</div>
                                <t2>日</t2>
                            </div>
                        </div>
                        <div className='modulecontent'>
                            <div style={{ display: 'flex', justifyContent: "space-between", alignItems: "cneter", width: "69px" }}>
                                <t2>生</t2>
                                <t2>日:</t2>
                            </div>
                            <DatePicker placeholder="選擇日期" id="birthday" defaultValue={moment(mmddformat(source.birthday), dateFormat)} />
                        </div>
                        <div className='modulecontent'>
                            <centerstyle>
                                <t2>出生體重:</t2>
                            </centerstyle>
                            <div style={{ display: 'flex' }}><Input placeholder="120" id="weight" defaultValue={source.weightbirth} />&nbsp;g</div>
                        </div>
                    </div>
                </Modal>
            </div>
        );
    }
}

export default Wardcarddetail;